import {setupModal} from './modal';

const modalSuccessMessage = document.querySelector('.modal--success-message');
const modalRecReg = document.querySelector('.modal--recommendations-registration');
const modalProgramRules = document.querySelector('.modal--program-rules');

setupModal(modalSuccessMessage);
setupModal(modalRecReg);
setupModal(modalProgramRules);

//   ,   ,       
const initModal = (modalId, modal, btn) => {
  switch (modalId) {
    case 'example':
      setupModal(modal, false, btn, false, true, true);
      break;
    default:
      setupModal(modal, false, btn, false, false, false);
      break;
  }
};

//  setupModal(modal, closeCallback, modalBtns, openCallback, noPrevDefault, preventScrollLock)
//      ,
//          - 
const initModals = () => {
  const modals = document.querySelectorAll('.modal:not(.is-initialized)');
  const modalBtns = document.querySelectorAll('[data-modal]');

  //    ,      
  if (modals.length) {
    modals.forEach((el) => {
      setTimeout(() => {
        el.classList.remove('modal--preload');
        el.classList.add('is-initialized');
      }, 100);
    });
  }

  if (modalBtns.length) {
    modalBtns.forEach((btn) => {
      const modalId = btn.dataset.modal;
      const modal = document.querySelector(`.modal--${modalId}`);
      if (modal) {
        initModal(modalId, modal, btn);
      }
    });
  }
};

window.initModals = initModals;

export {initModals};
